:root {
  /* Light mode (default) */
  --bg: #0f1724; /* use slate-900 as base dark background for premium look */
  --text: #e6eef8;
  --muted: #94a3b8;
  --card: #0b1220;
  --card-border: rgba(255,255,255,0.04);
  --header-bg: rgba(255,255,255,0.03);
  --header-border: rgba(255,255,255,0.04);
}

/* `.light` can be used if needed to force a light theme; default stays dark for this portfolio */
.light {
  --bg: #f8fafc;
  --text: #0f1724;
  --muted: #6b7280;
  --card: #ffffff;
  --card-border: rgba(2,6,23,0.06);
  --header-bg: rgba(2,6,23,0.04);
  --header-border: rgba(2,6,23,0.06);
}

html, body, #__next {
  height: 100%;
}

body{
  --tw-bg-opacity: 1;
  background-color: rgb(15 23 42 / var(--tw-bg-opacity, 1));
  --tw-text-opacity: 1;
  color: rgb(241 245 249 / var(--tw-text-opacity, 1));
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  background-color: var(--bg);
  color: var(--text);
  background-image: radial-gradient(1200px 600px at 10% 10%, rgba(124,58,237,0.04), transparent);
  transition: background-color 220ms cubic-bezier(.2,.9,.2,1), color 220ms cubic-bezier(.2,.9,.2,1);
  font-family: 'Inter', ui-sans-serif, system-ui, -apple-system, 'Segoe UI', Roboto, 'Helvetica Neue', Arial;
  font-weight: 400;
}

/* Heading font utility */
.font-heading {
  font-family: 'Rajdhani', 'Inter', ui-sans-serif, system-ui, -apple-system, 'Segoe UI', Roboto, 'Helvetica Neue', Arial;
  letter-spacing: -0.02em;
  font-weight: 700;
}

/* Slightly bolder subtitles */
.muted {
  color: var(--muted);
}

.container{
  margin-left: auto;
  margin-right: auto;
  max-width: 64rem;
  padding-left: 1.5rem;
  padding-right: 1.5rem;
}

.card {
  background-color: var(--card);
  border: 1px solid var(--card-border);
  border-radius: 0.75rem;
  padding: 1.5rem;
  transition: transform 220ms cubic-bezier(.2,.9,.2,1), box-shadow 220ms cubic-bezier(.2,.9,.2,1), background-color 220ms ease;
  box-shadow: 0 6px 18px rgba(2,6,23,0.6);
}

.card:hover {
  transform: translateY(-6px) scale(1.05);
  box-shadow: 0 14px 36px rgba(2,6,23,0.7);
}

/* Site header uses CSS variables so it can respond to theme changes */
.site-header {
  background-color: var(--header-bg);
  border-bottom: 1px solid var(--header-border);
}

.site-header a { color: var(--muted); }
.site-header a:hover { color: var(--text); }

/* Ensure buttons and other elements inherit text color from variables */
button, a, p, span, h1, h2, h3, h4 {
  color: inherit;
}
.hover\:bg-neutral-700:hover{
  --tw-bg-opacity: 1;
  background-color: rgb(64 64 64 / var(--tw-bg-opacity, 1));
}
.hover\:bg-neutral-800:hover{
  --tw-bg-opacity: 1;
  background-color: rgb(38 38 38 / var(--tw-bg-opacity, 1));
}
.hover\:text-neutral-200:hover{
  --tw-text-opacity: 1;
  color: rgb(229 229 229 / var(--tw-text-opacity, 1));
}
.hover\:text-white:hover{
  --tw-text-opacity: 1;
  color: rgb(255 255 255 / var(--tw-text-opacity, 1));
}
.hover\:brightness-105:hover{
  --tw-brightness: brightness(1.05);
  filter: var(--tw-blur) var(--tw-brightness) var(--tw-contrast) var(--tw-grayscale) var(--tw-hue-rotate) var(--tw-invert) var(--tw-saturate) var(--tw-sepia) var(--tw-drop-shadow);
}
.hover\:brightness-150:hover{
  --tw-brightness: brightness(1.5);
  filter: var(--tw-blur) var(--tw-brightness) var(--tw-contrast) var(--tw-grayscale) var(--tw-hue-rotate) var(--tw-invert) var(--tw-saturate) var(--tw-sepia) var(--tw-drop-shadow);
}
@media (min-width: 640px){

  .sm\:flex{
    display: flex;
  }

  .sm\:hidden{
    display: none;
  }

  .sm\:h-44{
    height: 11rem;
  }

  .sm\:grid-cols-2{
    grid-template-columns: repeat(2, minmax(0, 1fr));
  }

  .sm\:flex-row{
    flex-direction: row;
  }

  .sm\:text-3xl{
    font-size: 1.875rem;
    line-height: 2.25rem;
  }

  .sm\:text-6xl{
    font-size: 3.75rem;
    line-height: 1;
  }
}
@media (min-width: 1024px){

  .lg\:grid-cols-3{
    grid-template-columns: repeat(3, minmax(0, 1fr));
  }
}
